import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

class About extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.onRadioBtnClick = this.onRadioBtnClick.bind(this);

    this.state = {
      dropdownOpen: false,
      radioSelected: 2,
      isLoggedIn: false
    };
  }

  toggle() {
      this.setState({
        dropdownOpen: !this.state.dropdownOpen,
      });
  }

  onRadioBtnClick(radioSelected) {
      this.setState({
        radioSelected: radioSelected,
      });
  }

  componentWillMount(){
    if(cookies.get('token') !== undefined && cookies.get('token') !== null){
      this.setState({
        isLoggedIn: true
      });
    }
  }

  loading = () => <div className="animated fadeIn pt-1 text-center">Loading...</div>

  render() {
    // if(this.state.isLoggedIn){
      return (
        <div className="animated fadeIn">
          <h3>Mission Australia is a national Christian charity that has been helping vulnerable Australians move towards independence for 160 years.</h3>
          <p>Every day we support people by combatting homelessness, assisting disadvantaged families and children, addressing mental health issues, fighting substance dependencies, and much more.
          Weâ€™re generously supported by our funders, partners and tens of thousands of everyday Australians, who make the work of our tireless volunteers and staff possible.
          Together, we stand with Australians in need until they can stand for themselves.
          </p>
          <img src={'assets/img/about.jpg'}/>
        </div>
      );
    // } else {
    //   return <Redirect to='/login'/>
    // }
  }
}

export default About;
